/************** ****************/
/***** STATIC STARS! *******/
function staticStars(){
  const spaceGrid = document.querySelector("#navigation-starfield");
  
  //Create a new star element
  let newStar = document.createElement("span");
  newStar.classList.add("star");

  /* Give it a random size, call it once and store it in a variable so
  we use the same width and height */
  let randomSize = `${getRandomNum(3, 10)}px`
  newStar.style.width = randomSize;
  newStar.style.height = randomSize;
  
  //Place the new start element to the grid
  spaceGrid.append(newStar);
  
  //We give it a random grid column placement (40 columns grid)
  newStar.style.gridColumnStart = getRandomNum(1, 40);
  
  newStar.style.top = `${getRandomNum(1, 100)}%`;
  //random number for an animation delay, so they look different
  newStar.style.animationDelay = `${getRandomNum(1, 6)}s`;
  /******** *********/

}

for (var i = 0; i <= 50; i++){
  staticStars();
}


